.page {
	background-color: #fff;
}

header {
	display: flex;
	justify-content: center;
	margin-bottom: 64px !important;
	border-bottom: none !important;

	h1 {
		font-weight: 400 !important;
	}
}

.singular .entry-header {
	padding: 0 !important;
}

.entry-content,
.entry-summary {
	margin: 0 !important;
}

.wp-block-block-test-image-list {
	width: 960px;
	height: 100%;
	max-width: 100% !important;
}

/*! Flickity v2.3.0
https://flickity.metafizzy.co
---------------------------------------------- */

.flickity-enabled {
	position: relative;
	margin-bottom: 70px;
}

.flickity-enabled:focus {
	outline: none;
}

.flickity-viewport {
	overflow: hidden;
	position: relative;
	height: 720px;
}

.flickity-slider {
	position: absolute;
	width: 960px;
	height: 100%;

	img {
		height: 720px !important;
		object-fit: cover;
	}
}

/* draggable */

.flickity-enabled.is-draggable {
	-webkit-tap-highlight-color: transparent;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.flickity-enabled.is-draggable .flickity-viewport {
	cursor: move;
	cursor: -webkit-grab;
	cursor: grab;
}

.flickity-enabled.is-draggable .flickity-viewport.is-pointer-down {
	cursor: -webkit-grabbing;
	cursor: grabbing;
}

/* ---- flickity-button ---- */

.flickity-button {
	position: absolute;
	background: #cde1f0 !important;
	color: #0066b3 !important;
	border: none;
}

.flickity-button:hover {
	background: #4d94ca !important;
	color: #fff !important;
	cursor: pointer;
}

.flickity-button:focus {
	outline: none;
	box-shadow: 0 0 0 5px #4d94ca;
}

.flickity-button:active {
	opacity: 0.6;
}

.flickity-button:disabled {
	opacity: 0.3;
	cursor: auto;

	/* prevent disabled button from capturing pointer up event. #716 */
	pointer-events: none;
}

.flickity-button-icon {
	fill: currentColor;
}

/* ---- previous/next buttons ---- */

.flickity-prev-next-button {
	top: 50%;
	width: 48px;
	height: 48px;
	border-radius: 50%;

	/* vertically center */
	transform: translateY( -50% );
	padding: 0 !important;
}

.flickity-prev-next-button.previous {
	left: -100px;
}

.flickity-prev-next-button.next {
	right: -100px;
}

/* right to left */
.flickity-rtl .flickity-prev-next-button.previous {
	left: auto;
	right: 10px;
}

.flickity-rtl .flickity-prev-next-button.next {
	right: auto;
	left: 10px;
}

.flickity-prev-next-button .flickity-button-icon {
	position: absolute;
	left: 20%;
	top: 20%;
	width: 60%;
	height: 60%;
}

/* ---- page dots ---- */

.flickity-page-dots {
	position: absolute;
	width: 100%;
	bottom: -45px;
	padding: 0;
	margin: 0;
	list-style: none;
	text-align: center;
	line-height: 1;
}

.flickity-rtl .flickity-page-dots {
	direction: rtl;
}

.flickity-page-dots .dot {
	display: inline-block;
	width: 7px;
	height: 7px;
	margin: 3px 12px;
	background: #0066b3;
	border-radius: 50%;
	cursor: pointer;
}

.flickity-page-dots .dot.is-selected {
	width: 14px;
	height: 14px;
	margin: 0 12px;
}
